.card {
  background: white;
  border: 1px solid #DEE2E6;
  box-sizing: border-box;
  border-radius: 4px;
  height: 184px;
  width: 316px;
  text-align: center;
}

.emoji {
  font-size: 48px;
  line-height: 48px;
  color: #68717A;
  padding-top: 24px;
}

.title {
  font-weight: bold;
  font-size: 17px;
  line-height: 28px;
  text-decoration-line: underline;
  color: #000000;
  margin-top: 18px;
}

.description {
  margin-top: 12px;
  padding: 0 24px;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  color: #3F444A;
}