.content {
  position: relative;
  max-width: 988px;
  padding-top: 80px;
  margin: 0 auto;
}

.cards {
  display: inline-grid;
  width: 100%;
  gap: 19px;
  grid-template-columns:  repeat( auto-fill, 316px );
  justify-content: center;
}

.card {
  margin: 0 auto;
}

.input {
  margin: 0;
  max-width: 820px;
  width: 100%;
  height: 64px;
  padding: 0 32px;
  font-size: 24px;
  line-height: 21px;
  color: #ABB5BE;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #CED4DA;
  box-sizing: border-box;
  border-radius: 4px;
}

.input::placeholder {
  color: #ABB5BE;
}